.app {
  text-align: center;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 147, 255,.1);
}

.app h4, .app p, .app label{
	font-size: 4vw;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
}

.game-space, .non-board-space, .message-space{
	width: 92vw;
	margin: 2vw auto 0;
}

@media(min-aspect-ratio:9/16){
	.app p, .app label{
		font-size: 3vw;
	}
	.game-space, .non-board-space, .message-space{
		width: 84vw;
	}
}

@media(min-aspect-ratio:3/4){
	.app p, .app label{
		font-size: 2.5vw;
	}

	.game-space, .non-board-space, .message-space{
		width: 73vw;
		margin: 1vw auto 0;
	}
}

@media(min-aspect-ratio:9/10){
	.app p{
		font-size: 3.5vh;
	}
	.game-space{
		width: 60vw;
		float: left;
		padding-left:6vw;
	}
	.non-board-space{
		width: 30vw;
		float: right;
		padding-right:4vw;
	}
	.game-space, .non-board-space{
		margin: 4vw auto 0;
		height: 68vh;
	}
	.message-space{
		width: 80vw;
		display: inline-block;
	}
}

@media(min-aspect-ratio:1/1){
	.game-space{
		max-width: 55vw;
	}
}

@media(min-aspect-ratio:10/9){
	.game-space{
		max-width: 65vh;
	}
}

@media(min-aspect-ratio:4/3){
	.game-space, .non-board-space, .message-space{
		height: inherit;
	}
	.game-space, .non-board-space{
		margin: 6vw auto 0;
	}
	.game-space{
		width: 50vw;
		padding-left:9vw;
	}
	.non-board-space, .message-space{
		width: 30vw;
		float: right;
		padding-right:9vw;
	}

}

@media(min-aspect-ratio:16/9){
	.game-space{
		width: 40vw;
	}
	.non-board-space, .message-space{
		width: 45vw;
	}
	.game-space, .non-board-space{
		margin: 6vw auto 0;
	}
	.game-space, .non-board-space, .message-space{
		height: 40vw;
	}
	.game-space{
		float:left;
		padding-left: 7vw;
	}
	.non-board-space, .message-space{
		float: right;
		padding-right: 7vw;
		height: inherit;
	}
}



