.inkChoice{
  display: inline-block;
  background-color: white;
  color: black;
  border-radius: 50%;
  border: .5vw solid #000000;
  text-align: center;
  -webkit-transition: border-color color .4s;
  -o-transition: border-color color .4s;
  transition: border-color color .4s;
}

.inkChoice:hover{
  border-color: #0147FA;
  color: #0147FA;
}

@media(max-aspect-ratio:9/16){
  .inkChoice{
    height: 7vw;
    width: 7vw;
    margin: 0 .5vw;
  }
  .inkChoice h4{
    margin: 1.25vw 0 0 0;
  }
}

@media(min-aspect-ratio:9/16){
  .inkChoice{
    height: 6vw;
    width: 6vw;
    margin: 0 .5vw;
  }
  .inkChoice h4{
    margin: .7vw 0 0 0;
  }
}

@media(min-aspect-ratio:3/4){
  .inkChoice{
    height: 5vw;
    width: 5vw;
    margin: 0 .5vw;
  }
  .inkChoice h4{
    margin: .4vw 0 0 0;
  }
}

@media(min-aspect-ratio:9/10){
  .inkChoice{
    height: 4.5vw;
    width: 4.5vw;
    margin: .5vw .5vw .25vw;
  }
  .inkChoice h4{
    font-size: 3.5vw;
    margin: 0.35vw;
  }
}

@media(min-aspect-ratio:4/3){
  .inkChoice{
    border: .35vw solid black;
    height: 4vw;
    width: 4vw;
    margin: .75vw .65vw .25vw;
  }
  .inkChoice h4{
    font-size: 3.5vw;
    margin: .1vw 0 0 0;
  }
}

@media(min-aspect-ratio:16/9){
  .inkChoice{
    border: .25vw solid black;
    height: 3vw;
    width: 3vw;
    margin: .25vw;
  }
  .inkChoice h4{
    font-size: 2vw;
    margin: .4vw 0 0 0;
  }
}


