.inkChoices{
  margin: 4vw auto 0;
}

@media(max-aspect-ratio:9/16){
  .inkChoices{
    width: 90vw;
  }
}

@media(min-aspect-ratio:9/16){
  .inkChoices{
    width: 81vw;
  }
}

@media(min-aspect-ratio:3/4){
  .inkChoices{
    width: 72vw;
    margin: 2vw auto 0;
  }
}

@media(min-aspect-ratio:9/10){
  .inkChoices{
    width: 28vw;
    margin: 2vw 1vw 1vw;
  }
}

@media(min-aspect-ratio:4/3){
  .inkChoices{
    width: 30vw;
    margin: 2vw .5vw;
  }
}

@media(min-aspect-ratio:16/9){
  .inkChoices{
    width: 45vw;
  }
}



