.row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: nowrap;
      flex-flow: nowrap;
}

@media(min-aspect-ratio:9/16){
  .row{
    padding-left:3vw;
  }
}

@media(min-aspect-ratio:3/4){
  .row{
    padding-left:2vw;
  }

}

@media(min-aspect-ratio:9/10){
  .row{
    padding-left: 0;
  }
}



